import { type UserOutput } from '@/apollo/generated/types';
import { getFullName } from '@/utils/fullName';
import { type IUserDropdown } from './types';

export const buildDropDownData = (data: UserOutput[]): IUserDropdown[] =>
  data.map((datum: UserOutput) => ({
    key: datum._id,
    label: getFullName(datum.firstName, datum.middleName, datum.lastName),
    type: 'value',
    description: datum.title || undefined,
    value: datum,
  }));
